import "./App.css";
import { BrowserRouter as Router } from "react-router-dom";
import Styles from "./styles/Styles";

import PublicRoutes from "./routes/PublicRoutes";

function App() {
  return (
    <>
      <Router basename={process.env.PUBLIC_URL}>
        <PublicRoutes />
      </Router>
      <Styles />
    </>
  );
}

export default App;
